<!--商户与商品管理-->
<template>
  <div class="content">
    <el-menu :default-active="activeIndex" :style="{ height: '60px'}" mode="horizontal" @select="handleSelect">
      <el-menu-item index="1" style="margin: 0 10px">
        <span>在合作商户</span>
      </el-menu-item>
      <el-menu-item v-show="false" index="2" style="margin: 0 10px">
        <span>已暂停商户</span>
      </el-menu-item>
    </el-menu>
    <el-form class="account-plat-formitem" label-position="left" :model="ruleForm" ref="ruleForm" label-width="130px"
             style="text-align: left;font-size:20px;font-weight:
      bold;" @submit.native.prevent>
      <el-card class="account-plat-card1 account-plat-card2" shadow="always" :body-style="cardBodyStyle" style="">
        <div style="margin-top: 10px">
          <el-row :gutter="24">
            <el-col :md="16">
              <el-form-item label="商户名称:" prop="mbmi_mer_name">
                <el-input v-model="ruleForm.mbmi_mer_name" style="width: 300px;"></el-input>
              </el-form-item>
            </el-col>
            <el-col :md="4">
              <el-button style="float:right;margin-left: 20px;" type="primary" @click="clearQuery('ruleForm')">清空
              </el-button>
              <el-button style="float:right;" type="primary" @click="select_table">查询</el-button>
            </el-col>
            <el-col :md="4" v-show="false">
              <el-button style="float:left;" type="primary" @click="new_merchant"><i
                class="el-icon-edit">新增商户</i></el-button>
            </el-col>
          </el-row>
        </div>
      </el-card>
    </el-form>

    <!--  页面表格 start -->
    <el-card>
      <el-table
        class="account-plat-fixed-right account-plat-fixed "
        border
        ref="tableData"
        :data="tableData"
        tooltip-effect="dark"
        @sort-change="sortColumn"
        :row-style="{'height':'10px'}"
        :cell-style="{'padding':'5px 0px'}"
        :header-cell-style="{'background': '#4B86BC','color':'#ffffff','borderRight': '1px solid #E3E5EB' ,'padding':'0px','height':'45px','line-height':'45px' }"
      >
        <!--        <el-table-column
                  type="selection"
                  :selectable="isSelect"
                  width="55">
                </el-table-column>-->
        <el-table-column
          fixed
          align="center"
          type="index"
          label="序号"
          width="50">
        </el-table-column>
        <el-table-column
          prop="mbmi_mer_name"
          label="商户名称"
          align="center"
          show-overflow-tooltip
          width="300"
        >
        </el-table-column>
        <el-table-column
          prop="mbmi_contacts"
          label="联系人"
          align="center"
          show-overflow-tooltip
          width="150"
        >
        </el-table-column>
        <el-table-column
          prop="mbmi_phone"
          label="联系电话"
          align="center"
          show-overflow-tooltip
          width="200"
        >
        </el-table-column>
        <el-table-column
          prop="mbmi_license_code"
          label="统一社会信用代码"
          align="center"
          show-overflow-tooltip
          width="300"
        >
        </el-table-column>
        <!--        <el-table-column-->
        <!--          prop="mbmi_account_name"-->
        <!--          label="收款账户名称"-->
        <!--          align="center"-->
        <!--          show-overflow-tooltip-->
        <!--          width="150"-->
        <!--        >-->
        <!--        </el-table-column>-->
        <!--        <el-table-column-->
        <!--          prop="mbmi_account_code"-->
        <!--          label="收款账号"-->
        <!--          align="center"-->
        <!--          show-overflow-tooltip-->
        <!--          width="150"-->
        <!--        >-->
        <!--        </el-table-column>-->
        <!--        <el-table-column-->
        <!--          prop="mbmi_account_bank"-->
        <!--          label="收款账号开户行"-->
        <!--          align="center"-->
        <!--          show-overflow-tooltip-->
        <!--          width="150"-->
        <!--        >-->
        <!--        </el-table-column>-->
        <el-table-column
          fixed="right"
          label="操作"
          min-width="300"
          align="center"
        >
          <template slot-scope="scope" v-if="scope.row.lai_detail_status !=='4'">
            <el-tooltip content="商户与商品信息管理" placement="top">
              <el-button
                type="primary" icon="el-icon-user" circle
                size="mini"
                @click.native.stop="administration(scope.$index,scope.row)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--  页面表格 end   -->
    </el-card>
    <!--页面 end-->
    <!--page start-->
    <el-pagination
      style="margin-top: 20px"
      background
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="queryParam.currentPage"
      :page-sizes="[10, 20, 50, 100,200]"
      :page-size="queryParam.pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="queryParam.totalRow">
    </el-pagination>
    <!--page end-->
    <!--  弹出窗口  -->
    <el-dialog :visible.sync="essentialFormVisible"
               :close-on-press-escape="false"
               :close-on-click-modal="false"
               :show-close="false"
               class="account-plat-dialog"
               width="80%"

    >
      <div slot="title" style="text-align: left;font-size:20px;font-weight: bold;">
        <span>{{ this.tableDialgTitle }}</span>
      </div>
      <!--form start-->
      <el-form class="account-plat-formitem" label-position="left" :model="essentialForm" :rules="essentialFormrule"
               ref="essentialForm" label-width="130px"
               style="text-align: left;font-size:20px;font-weight: bold;">
        <el-row :gutter="24" style="font-size: 16px;margin: 10px 0">
          <el-col :md="24">
            <div>基本信息设置</div>
          </el-col>
        </el-row>
        <el-row :gutter="24" style="margin-left: 10px">
          <el-col :md="12" style="margin: 10px 0">
            <el-form-item label="商户名称:" prop="mbmi_mer_name">
              <el-input :maxlength="20" placeholder="最多支持20个字" v-model="essentialForm.mbmi_mer_name"
                        :disabled="disabled"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :md="12" style="margin: 10px 0">
            <el-form-item label="统一社会信用代码:" prop="mbmi_license_code">
              <el-input :maxlength="18" placeholder="请输入18个位统一社会信用代码" v-model="essentialForm.mbmi_license_code"
                        :disabled="disabled"
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="24" style="margin-left: 10px">
          <el-col :md="12" style="margin: 10px 0">
            <el-form-item label="联系人:" prop="mbmi_contacts">
              <el-input :maxlength="20" placeholder="最多支持20个字" v-model="essentialForm.mbmi_contacts"
                        :disabled="disabled"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :md="12" style="margin: 10px 0">
            <el-form-item label="联系电话:" prop="mbmi_phone">
              <el-input :maxlength="11" placeholder="最多支持11个位" v-model="essentialForm.mbmi_phone" :disabled="disabled"
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="24" style="margin:10px 0 10px 10px;">
          <el-col :md="24">
            <el-form-item label="详细地址:" prop="mbmi_address">
              <el-input
                type="textarea"
                autosize
                :maxlength="30"
                :autosize="{ minRows: 2, maxRows: 4}"
                placeholder="请输入内容,最多支持30个字"
                v-model="essentialForm.mbmi_address"
                :disabled="disabled"
              >
              </el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <div class="box" style="margin: 20px 10px">
          <el-button type="primary" style="float: right" v-show="false">提交</el-button>
        </div>
        <el-row :gutter="24" style="font-size: 16px;margin: 20px 0" v-show="add_itemshow">
          <el-col :md="17">
            <div>商品信息设置</div>
          </el-col>
          <el-col :md="7">
            <el-button @click="add_item">添加商品</el-button>
          </el-col>
        </el-row>
        <el-table
          v-show="add_itemshow"
          :data="essentialForm.tableData_prize"
          @sort-change="sortColumn"
          style="margin-left: 50px;width: 94%;margin-bottom: 50px;overflow-y:auto;max-height: 300px;"
          class="account-plat-fixed-right account-plat-fixed "
          border
          tooltip-effect="dark"
          :row-style="{'height':'0px'}"
          :cell-style="{'padding':'5px 0px'}"
          :header-cell-style="{'background': '#4B86BC','color':'#ffffff','borderRight': '1px solid #E3E5EB' ,'padding':'0px','height':'45px','line-height':'45px' }"
        >
          <el-table-column
            align="center"
            type="index"
            label="序号"
            width="50">
          </el-table-column>
          <el-table-column align="center" width="150">
            <template slot="header" slot-scope="scope">
              <i style="color:#F56C6C;">*</i>
              <span style="color:#fff;">商品图片</span>
            </template>
            <template slot-scope="scope">
              <el-button @click="onOpen(scope.$index, scope.row)" v-if="scope.row.mbgi_goods_img_path ===''">上传照片
              </el-button>
              <!--                c:/temp/target/uploaddir//13112345678///20211214092930276.png-->
              <div @click="onOpen(scope.$index, scope.row)">
                <el-image style="width: 80px;height: 60px;" :src="scope.row.mbgi_goods_img_path"
                          v-if="scope.row.mbgi_goods_img_path !==''"/>
              </div>
            </template>
          </el-table-column>
          <el-table-column align="center" width="200">
            <template slot="header" slot-scope="scope">
              <i style="color:#F56C6C;">*</i>
              <span style="color:#fff;">商品名称</span>
            </template>
            <template slot-scope="scope">
              <el-form-item
                :prop="'tableData_prize.' +scope.$index + '.lpi_caption'"
                label-width="0"
              >
                <el-input
                  :disabled="scope.row.disabled_mbgi_goods_name"
                  type="text"
                  v-model="scope.row.mbgi_goods_name"
                  autocomplete="off"
                  :maxlength="6" placeholder="最多可输入6个字符"
                ></el-input>
              </el-form-item>
            </template>
          </el-table-column>
          <el-table-column align="center" width="200">
            <template slot="header" slot-scope="scope">
              <i style="color:#F56C6C;">*</i>
              <span style="color:#fff;">规格</span>
            </template>
            <template slot-scope="scope">
              <el-form-item
                :prop="'tableData_prize.' +scope.$index + '.mbgi_goods_describer'"
                label-width="0"
              >
                <el-input
                  :disabled="scope.row.disabled_mbgi_goods_describer"
                  type="text"
                  v-model="scope.row.mbgi_goods_describer"
                  autocomplete="off"
                  :maxlength="6" placeholder="最多可输入6个字符"
                ></el-input>
              </el-form-item>
            </template>
          </el-table-column>
          <el-table-column align="center" width="200">
            <template slot="header" slot-scope="scope">
              <i style="color:#F56C6C;">*</i>
              <span style="color:#fff;">原价(元)</span>
            </template>
            <template slot-scope="scope">
              <el-form-item
                :prop="'tableData_prize.' +scope.$index + '.mbgi_money_1'"
                :rules="essentialFormrule.disabled_mbgi_money_1"
                label-width="0"
              >
                <el-input
                  :disabled="scope.row.disabled_mbgi_money_1"
                  type="text"
                  v-model="scope.row.mbgi_money_1"
                  autocomplete="off"
                  :maxlength="4" placeholder="输入范围在0至9999之间"
                ></el-input>
              </el-form-item>
            </template>
          </el-table-column>
          <el-table-column
            label="操作"
            min-width="200"
            align="center"
          >
            <template slot-scope="scope">
              <el-button v-show="scope.row.preserve_show" @click="preserve(scope.$index, scope.row)">保存</el-button>
              <el-button v-show="scope.row.revise_show" @click="revise(scope.$index, scope.row)">修改</el-button>
              <el-button v-show="scope.row.cut_off_show" @click="cut_off(scope.$index, scope.row)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-form>
      <!--form end-->
      <div slot="footer" class="dialog-footer">
        <el-button v-show="false" @click="clearQuery('essentialForm')">重 置</el-button>
        <el-button @click="cancelForm('essentialForm')">关 闭</el-button>
        <el-button v-show="false" type="primary" @click="submitForm('essentialForm')">确 定</el-button>
      </div>
    </el-dialog>
    <!--截图功能-->
    <el-dialog :title="options.title" :visible.sync="dialogVisible" :before-close="handleClose">
      <div class="el-cropper">
        <div>
          <div style="margin: 10px 0">
            <div style="font-weight: 600">操作说明：</div>
            <div>通过鼠标滚轮调整图片大小</div>
            <div>通过鼠标左键调整裁剪框位置</div>
          </div>
          <div class="cropper-content">
            <div class="cropper">
              <vueCropper ref="cropper"
                          :img="options.img"
                          :outputSize="options.outputSize"
                          :outputType="options.outputType"
                          :full="options.full"
                          :canMove="options.canMove"
                          :canMoveBox="options.canMoveBox"
                          :original="options.original"
                          :autoCrop="options.autoCrop"
                          :autoCropWidth="options.autoCropWidth"
                          :autoCropHeight="options.autoCropHeight"
                          :fixedBox="options.fixedBox"
                          :enlarge="options.enlarge"
                          :centerBox="options.centerBox"
                          :fixedNumber="options.fixedNumber"
                          :fixed="options.fixed"
                          :maxImgSize="options.maxImgSize"
                          :mode="options.mode"
                          :infoTrue="options.infoTrue"
                          :high="options.high"
                          @realTime="realTime"
              ></vueCropper>
            </div>
            <div class="show-preview" :style="{'overflow': 'hidden'}" v-show="false">
              <div class="wait">预览区域：</div>
              <div class="preview">
                <img :src="imgsrc" alt="" :style="scaleFun">
              </div>
              <!--              <div class="detailsclass">-->
              <!--                <p class="">截图详情：</p>-->
              <!--                <p v-text="options.details" :title="options.details"></p>-->
              <!--              </div>-->
            </div>
          </div>
        </div>


        <div style="  margin-top: 10px;">
          <el-row>
            <!-- 选择图片 -->
            <el-col :span="6">
              <div>
                <label class="el-button el-button--default" for="uploads3" >选择图片
                  <input type="file" ref="reference" id="uploads3" style="position:absolute; clip:rect(0 0 0 0);"
                         accept="image/png, image/jpeg, image/jpg" @change="uploadImg($event, 1)"/>
                </label>

              </div>
            </el-col>
            <!-- 功能按钮 -->
            <el-col :span="6">
              <div>
                <!--                <el-button type="ghost" @click="changeScale(1)" icon="plus-round" class="l-btn">+</el-button>-->
                <!--                <el-button type="ghost" @click="changeScale(-1)" icon="minus-round" class="l-btn">-</el-button>-->
                <el-button @click="rotateLeft"><span style="font-weight: 600;">↺</span></el-button>
                <el-button @click="rotateRight"><span style="font-weight: 600;">↻</span></el-button>
              </div>
            </el-col>
            <el-col :span="12">
              <div>
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button @click="oncropped('base64')">确 定</el-button>
              </div>
            </el-col>

          </el-row>
        </div>


      </div>
    </el-dialog>
  </div>
</template>

<script>
import {VueCropper} from 'vue-cropper';
import axios from "axios";

export default {
  name: "MerchantAndCommodity",
  components: {
    VueCropper,
  },
  data() {
    return {
      activeIndex: '1',
      cardBodyStyle: 'padding-top:10px;padding-bottom:10px;',
      ruleForm: {
        mbmi_mer_name: '',
      },
      tableData: [],//表格数据
      sortOrderCol: '',
      /** 查询参数 */
      queryParam: {
        totalRow: 0,
        currentPage: 1,
        pageSize: this.customGlobal.pageSize,
      },
      /** 是否显示隐藏窗口 */
      essentialFormVisible: false,
      tableDialgTitle: '',//新增，修改
      /** 新增编辑表单参数 */
      essentialForm: {
        tableData_prize: [],//礼品表格
        mbmi_mer_name: '',
        mbmi_contacts: '',
        mbmi_phone: '',
        mbmi_address: '',
        mbmi_license_code: '',
        mbgi_goods_name:'',
        mbgi_goods_describer:'',
        mbgi_money_1:''
      },
      /** 表单验证 */
      essentialFormrule: {
        // disabled_mbgi_money_1: [
        //   {required: true, validator: disabled_mbgi_money_1, trigger: 'blur'}
        // ],
      },
      new_but: '',//区分新增与修改
      add_itemshow: '',//显示隐藏新增商品
      mbmi_status: '',
      key: '',//标签页
      mbmi_flag: '',//区分银行与商户
      disabled: false,//表单限制输入开关
      //截图功能
      options: {
        img: "",
        outputSize: 1,//	 裁剪生成图片的质量 0.1 - 1
        outputType: 'png | jpeg | jpg',	//裁剪生成图片的格式 jpeg || png || webp
        full: true,//是否输出原图比例的截图,true是不失真
        canMove: true,//图片是否可以移动
        canMoveBox: true,//	截图框能否拖动
        original: false,//图片按照原始比例渲染,true会超出屏幕区域，false是适应屏幕区域
        autoCrop: true,//是否默认生成截图框
        autoCropWidth: 250,
        autoCropHeight: 250,
        fixedBox: true,//固定截图框大小 不允许改变
        enlarge: 1,
        centerBox: true,//截图框是否被限制在图片里面
        fixed: true,//	是否开启截图框宽高固定比例
        fixedNumber: [1, 1],// 截图框的宽高比例 [宽度, 高度]
        maxImgSize: 2000,
        mode: 'contain',//整个图片填充裁剪框 contain , cover, 100px, 100% auto
        high: true,
        infoTrue: true,//true截图框展示真实图片的宽高,false截图框内显示可缩放图片

        title: '图像裁剪',
        details: '选择图片，上传',
      },
      dialogVisible: false,
      imgsrc: '',
      mbgi_goods_img_path: '',//图片地址
      mbgi_goods_img_path1: '',
      index: '',
      mbgi_mer_uuid:'',//商户
      mbgi_uuid:'',//唯一id
      tableData_gift:[],//存礼品的数据
    }
  },
  created() {
    this.mbmi_status = '2';
    this.select_table();
  },
  // 预览实时缩放
  computed: {
    scaleFun: function () {
      var scale = this.scaleratioXY;
      return `transform:scale(${scale},${scale})`;
    }
  },
  methods: {
    handleSelect(key, keyPath) {
      console.log('上：' + key, keyPath);
      this.queryParam.currentPage = 1;
      this.key = key;
      if (this.key === '1') {
        this.mbmi_status = '2';
      } else if (this.key === '2') {
        this.mbmi_status = '1';
      }
      this.select_table();
    },
    /**  表格列排序设置   */
    sortColumn(column) {
      //  console.log("已经排序项：" + JSON.stringify(column));
      let orderFlag = '';
      if (column.order != null) {
        if (column.order === "descending") {
          orderFlag = 'desc';
        } else if (column.order === "ascending") {
          orderFlag = 'asc';
        }
        //  console.info("属性名称："+ column.prop+",排序方式："+column.order);
        this.sortOrderCol = column.prop + " " + orderFlag;
        //  console.info("用户选择排序列："+this.sortOrderCol);
        this.select_table();
      } else {
        this.sortOrderCol = '';
      }

    },
    /**----分页方法-------start----*/
    handleSizeChange(val) {
      //  console.log(`每页 ${val} 条`);
      this.queryParam.pageSize = val;
      this.select_table();
    },
    handleCurrentChange(val) {
      //  console.log(`当前页: ${val}`);
      this.queryParam.currentPage = val;
      this.select_table();
    },
    //商户与商品信息管理
    administration(index, row) {
      this.mbgi_mer_uuid = row.mbmi_uuid;
      this.essentialForm.mbmi_mer_name = row.mbmi_mer_name;
      this.essentialForm.mbmi_contacts = row.mbmi_contacts;
      this.essentialForm.mbmi_phone = row.mbmi_phone;
      this.essentialForm.mbmi_address = row.mbmi_address;
      this.essentialForm.mbmi_license_code = row.mbmi_license_code;
      this.mbmi_flag = row.mbmi_flag;
      this.essentialFormVisible = true;
      this.tableDialgTitle = '商户与商品信息管理';
      this.add_itemshow = true;
      this.new_but = '2';
      this.disabled = true;
      this.select_gift();

    },
    //新增商户按钮
    new_merchant() {
      this.disabled = false;
      this.essentialFormVisible = true;
      this.new_but = '1';
      this.add_itemshow = false;
      this.tableDialgTitle = '新增商户';
      this.essentialForm.mbmi_mer_name = '';
      this.essentialForm.mbmi_contacts = '';
      this.essentialForm.mbmi_phone = '';
      this.essentialForm.mbmi_address = '';
      this.essentialForm.mbmi_license_code = '';
    },
    //重置
    clearQuery(formName) {
      this.$refs[formName].resetFields();
    },
    //查询
    select_table() {
      let that = this;
      let _jsondata_ = {
        mbmi_bank_center_id: that.customGlobal.userInfoExt.cui_bank_center_id,
        mbmi_province_id: that.customGlobal.userInfoExt.cui_province_id,
        mbmi_city_id: that.customGlobal.userInfoExt.cui_city_id,
        mbmi_county_id: that.customGlobal.userInfoExt.cui_county_id,
        mbmi_station_id: that.customGlobal.userInfoExt.cui_station_id,
        mbmi_flag: '1',
        mbmi_mer_name: that.ruleForm.mbmi_mer_name,
