  display: block;
  visibility: hidden;
}

/*标题*/
.el-cropper .tit {
  text-align: left;
  height: 42px;
  /*padding: 5px 0px;*/
  line-height: 40px;
  background-color: #fff;
  border-bottom: 1px solid lightgray
}

/*功能btn*/
.el-cropper .l-btn {
  display: inline-block;
  outline: none;
  resize: none;
  border: none;
  padding: 5px 20px;
  background: #409EFF;
  color: #fff;
  border: solid 1px #409EFF;
  border-radius: 3px;
  font-size: 16px;
  margin-left: 1.3vw;
}

.el-cropper .btn:nth-child(2) {
  margin-left: 10px;
}

/*中盒*/
.el-cropper .cropper-content {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-around;
  overflow: hidden;
}

/*详情*/
.el-cropper .detailsclass p {
  text-align: left;
  font-family: "Arial", "Microsoft YaHei", "黑体", "宋体", sans-serif;
}

/**/
.el-cropper .detailsclass p:first-child {
  text-align: left;
  font-weight: 400;
  color: black;
}

/**/
.el-cropper .detailsclass p:last-child {
  text-align: left;
  font-size: 14px;
  margin-top: -5px;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 4;
  overflow: hidden;
  font-family: "Arial", "Microsoft YaHei", "\9ED1\4F53", "\5B8B\4F53", sans-serif;
  color: #2c3e50;

}

/**/
.el-cropper .cropper {
  width: 65%;
  height: 400px;
  overflow: hidden;
}

/**/
.el-cropper .show-preview {
  /*flex:1;*/
  width: 230px;
  height: auto;
  text-align: left;
  padding: 0px 0px 0px 20px;
  /*margin-top: 20px;*/
  /* display: flex; */
  /* flex-direction:column; */
  /* align-items:stretch; */
}

.el-cropper .detailsclass {
  width: 200px;
  margin-top: 30px;
}

.el-cropper .preview {
  border: 1px solid #ccc;
  overflow: hidden;
}

.show-preview img {
  transform-origin: 0 0 0
}

.wait {
  width: 200px;
  padding: 0px 0px 15px 0px;
  font-weight: 400;
  color: black;
}

.preview {
  width: 200px;
  height: 200px;
  background: #ddd;
}
.account-plat-dialog /deep/ .el-dialog__body {
  padding: 0px 20px !important;
  color: #606266 !important;
  font-size: 14px !important;
  word-break: break-all !important;
  margin-left: 0px !important;
}
.content >>> .el-table::before {
  content: '';
  position: absolute;
  background-color: transparent;
  z-index: 1;
}
</style>



